* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.div-buttons{
  margin-top: 200px;
  text-align: center;
  user-select: none;
}

.btn.task-1{
  width: 200px;
  height: 100px;
  margin: 20px;
}

.btn-disabled{
  opacity: 0.3;
}

input[id="datetime-picker"]{
  height: 35px;
}

button[data-start]{
  width: 60px;
  height: 35px;
  border-radius: 10px;
  background-color: green;
  color: white;
  border-color: seagreen;
}

.timer {
  display: flex;
  flex-wrap: nowrap;
}

.field {
  display: flex;
  flex-direction: column;
  margin: 10px;
}

.value {
  text-align: center;
  font-size: 25px;
}

.label {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 500;
}

.form input {
  height: 35px;
  margin-right: 15px;
  width: 80px;
}

.form button {
  height: 45px;
  background-color: rgb(1, 156, 79);
  color: white;
  border: 2px solid green;
  margin-left: 20px;
  font-size: 18px;

}

.form button:hover{
  background-color:rgb(6, 97, 51);
  border: 2px solid green;
}

